'use strict';

(function ($) {

    $.fn.bindWithDelay = function (type, data, fn, timeout, throttle) {

        if ($.isFunction(data)) {
            throttle = timeout;
            timeout = fn;
            fn = data;
            data = undefined;
        }

        // Allow delayed function to be removed with fn in unbind function
        fn.guid = fn.guid || $.guid && $.guid++;

        // Bind each separately so that each element has its own delay
        return this.each(function () {

            var wait = null;

            function cb() {
                var e = $.extend(true, {}, arguments[0]);
                var ctx = this;
                var throttler = function throttler() {
                    wait = null;
                    fn.apply(ctx, [e]);
                };

                if (!throttle) {
                    clearTimeout(wait);wait = null;
                }
                if (!wait) {
                    wait = setTimeout(throttler, timeout);
                }
            }

            cb.guid = fn.guid;

            $(this).bind(type, data, cb);
        });
    };
})(jQuery);

(function ($) {
    $(document).on('click', '#close-search, #search-open-menu, .search-icon-menu > a', function (e) {
        e.preventDefault();

        $('body').toggleClass('search-open');

        if ($('body').hasClass('search-open')) {
            $('.search-input').focus();
        }
    });

    $('.search-input').bindWithDelay('keyup', '.search-input', function () {

        // Current search value
        var value = $(this).val();

        $.ajax({
            url: searchwebsite.ajaxurl,
            type: 'post',
            data: {
                action: 'search_website',
                q: value
            },
            success: function success(response) {
                if (response.length <= 0) {
                    $('#search-informations').html('');
                    $('#search-events').html('');
                    return false;
                }
                response = $.parseJSON(response);
                $('#search-informations').html(response.informations);
                $('#search-events').html(response.events);
            }
        });
    }, 1000);
})(jQuery);
